<template>
  <div>
    <list-content :datas="datas" :title="`您所在的城市：${location}`">
      <div class="load-more">
        <el-button v-if="showLoad" type="text" :loading="loading" @click="loadMore">加载更多..</el-button>
        <div v-else>没有更多了......</div>
      </div>
    </list-content>
  </div>
</template>
<script>
import listContent from './sub/list.vue';
// import axios from 'axios';
import BMap from 'BMap';
export default {
  data () {
    return {
      datas: [],
      pageSize: 15,
      page: 1,
      loading: false,
      showLoad: true,
      type: '',
      location: '娄底'
    };
  },
  components: {
    listContent
  },
  watch: {
    // 切换不同的参数使用同一个路由，路由的params不会传值，解决的方法:
    // 1、将传值的参数改为query
    // 2、使用this.$router.push({path: '/path/${id}'});进行传值
    '$route' (to, from) {
      this.datas = [];
      this.type = to.query.type ? to.query.type : 'good';
      this.getContent(this.$route.query.type, 1);
    }
  },
  methods: {
    // 获取地址
    addressDetail () {
      var geolocation = new BMap.Geolocation(); // 调用百度地图api 中的获取当前位置接口
      geolocation.getCurrentPosition(res => {
        console.log(res);
        if (res.district) {
          this.location = res.address.province + '/' + res.address.city + '，您所在的区域： ' + res.district;
        } else {
          this.location = res.address.province + '/' + res.address.city;
        }
      });
      // var geolocation = new BMap.Geolocation(); // 调用百度地图api 中的获取当前位置接口
      // geolocation.getCurrentPosition(res => {
      //   console.log(res);
      //   let currentLat = res.point.lat;
      //   let currentLon = res.point.lng;
      //   var pt = new BMap.Point(currentLon, currentLat);
      //   var geoc = new BMap.Geocoder();
      //   geoc.getLocation(pt, ret => {
      //     var addComp = ret.addressComponents;
      //     console.log(addComp);
      //   });
      // });
      // if (navigator.geolocation) {
      //   navigator.geolocation.getCurrentPosition(res => {
      //     axios.get('http://api.map.baidu.com/geocoder/v2/?callback=renderReverse&location=' + res.cord.latitude + ',' + res.cord.longitude + '&output=json&pois=1&ak=DxTVZ6tSQkzkkGK4lGbB3rXPwDmWWwEw').then(ret => {
      //       this.location = ret.addressComponent.district;
      //     }).catch(err => {
      //       this.$alert(err, '提示', {
      //         type: 'danger'
      //       });
      //     });
      //   }, err => {
      //     this.location = '涟源';
      //     console.log('获取当前位置错误！,错误原因： ');
      //     console.log(err);
      //   });
      // } else {
      //   var geolocation = new BMap.Geolocation(); // 调用百度地图api 中的获取当前位置接口
      //   geolocation.getCurrentPosition(res => {
      //     console.log(res);
      //     this.location = res.address.province + ' ' + res.address.city;
      //   });
      // }
    },
    loadMore () {
      this.getContent(this.type, this.page++);
    },
    getContent (type, page = this.page) {
      this.loading = true;
      let obj = {
        limit: this.pageSize,
        page: page,
        tab: type
      };
      this.$api.mock.cnode.getTopics(obj).then(ret => {
        if (ret) {
          this.datas = [...this.datas, ...ret];
          // console.log(this.datas);
          // for (let i = 0; i < data.length; i++) {
          //   this.$set(this.datas, i, data[i]);
          // }
        } else {
          this.showLoad = false;
        }
        this.loading = false;
      });
    }
  },
  created () {
    this.type = this.$route.query.type ? this.$route.query.type : 'good';
    this.getContent(this.type);
    this.addressDetail();
  }
};
</script>
<style lang="scss" rel="stylesheet/scss">
  @import '../../assets/scss/_index';
  @import "../../assets/scss/mixin";
  .load-more {
    display: block;
    padding: 10px 0;
  }
</style>



